.drawer {
  margin-inline: auto;
  width: 100%;
  height: calc(100vh - 75px);
  background-color: #1B1310;
  position: fixed;
  width: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  color: #fff;
  visibility: hidden;
  /* 非表示 */
  transform: translateX(0%);
  transition: visibility 0.3s ease, opacity 0.3s ease, transform 0.3s ease;
  /* スムーズなアニメーション */
  z-index: 100;
}
@media screen and (min-width: 768px) {
  .drawer {
    visibility: hidden;
    display: none;
  }
}

.is-drawerActive {
  overflow: hidden;
}

.drawer.is-drawerActive {
  visibility: visible;
  /* 表示する */
  transform: translateX(0);
  /* 元の位置に戻す */
}

